/* eslint-disable no-undef */
import { BrowserWindow } from 'electron';
import { TestRunner } from '../../shared/Types/Tests.type';
// eslint-disable-next-line import/no-cycle
import { createTestsForOfer } from './OferTests';
import { createTestsForYassur } from './YassurTests';
import { HelicopterPlatform } from '../../shared/Types/LogTypes';

export default function TestArrayFactory(
  platform: HelicopterPlatform,
  mainWindow: BrowserWindow
): TestRunner[] | null {
  if (platform === 'Ofer') return createTestsForOfer(mainWindow);
  if (platform === 'Yassur') return createTestsForYassur(mainWindow);
  return null;
}
