/* eslint-disable no-await-in-loop */
/* eslint-disable import/no-cycle */
/* eslint-disable no-undef */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-use-before-define */
import { BrowserWindow } from 'electron';
import {
  parseDiscretesLog,
  parseLog,
  recognizeHelicopterPlatform,
} from '../utils/DataParser';
import { TestRunner } from '../../shared/Types/Tests.type';
import {
  DisplayATPResult,
  DisplayErrorMsg,
  delay,
} from '../utils/TestsHelpers';
import TestArrayFactory from './AtpFactory';
import emitToClient from '../IPC/EmitToClient';
import {
  HelicopterPlatform,
  ParsedDiscretes,
  ParsedLog,
} from '../../shared/Types/LogTypes';
import { DataCode, OpCode, WriteToCMod } from '../CMod/CModManager';

export default class TesterManager {
  private static instance: TesterManager;

  public EcarosLog: ParsedLog | null = null;

  public EcarosDiscretes: ParsedDiscretes | null = null;

  public Tests: TestRunner[] = [];

  private mainWindow: BrowserWindow;

  private helicopterType: HelicopterPlatform | undefined = undefined;

  private isAllTestsPasses: Boolean = true;

  constructor(browserWindow: BrowserWindow) {
    this.mainWindow = browserWindow;
  }

  public static getInstance(
    window: BrowserWindow = this.instance.mainWindow
  ): TesterManager {
    if (!TesterManager.instance) {
      TesterManager.instance = new TesterManager(window);
    }
    return TesterManager.instance;
  }

  public static OnLogReceived(data: string | undefined) {
    if (this.instance.helicopterType === undefined) {
      this.instance.helicopterType = recognizeHelicopterPlatform(data);
      this.startStreamHeightCommand();
    }
    if (data)
      TesterManager.getInstance().EcarosLog = parseLog(
        data,
        this.instance.helicopterType === 'Yassur'
      );
  }

  public static OnDiscreteStatusReceived(data: string | undefined) {
    if (data)
      TesterManager.getInstance().EcarosDiscretes = parseDiscretesLog(data);
  }

  public CheckVersion() {
    this.Tests = TestArrayFactory('Ofer', this.mainWindow) ?? [];
    this.helicopterType = 'Ofer';
    // if (this.helicopterType)
    //   this.Tests = TestArrayFactory(this.helicopterType, this.mainWindow) ?? [];
    // else DisplayErrorMsg(this.mainWindow, 'ECaros   ');
  }

  public getHelicopterType(): string {
    return this.helicopterType ?? 'err';
  }

  public async StartTester(testsToRun: string[]) {
    const tests = this.extractRequestedTests(testsToRun);
    this.isAllTestsPasses = true;
    // eslint-disable-next-line no-restricted-syntax
    for (const test of tests) {
      await this.runTest(test);
    }
    emitToClient(this.mainWindow, 'finished_tests_alert', null);
    DisplayATPResult(this.mainWindow, this.isAllTestsPasses);
  }

  private async runTest(test: TestRunner) {
    emitToClient(this.mainWindow, 'test_in_progress', test.name);
    await delay(1000);

    const result = await test.testFunc(); // this will wait for the promise to resolve before moving to the next line
    emitToClient(this.mainWindow, 'test_result', result);

    if (!result.result) {
      DisplayErrorMsg(this.mainWindow, result.message);
      this.isAllTestsPasses = false;
    }
    await delay(1000); // waits for 1 second before proceeding to the next iteration
  }

  private extractRequestedTests(request: string[]): TestRunner[] {
    return this.Tests.filter((test: TestRunner) => request.includes(test.name));
  }

  private static startStreamHeightCommand() {
    if (this.instance.helicopterType === 'Yassur')
      WriteToCMod(OpCode.STREAM_GOVA, DataCode.StreamGovaOnRs);
    else WriteToCMod(OpCode.STREAM_GOVA, DataCode.StreamGovaOnArinc);
  }
}
