import { app } from 'electron';
import { promises as fsPromises } from 'fs';
import path from 'path';
import { Configuration } from '../../shared/Types/Configuration.type';

const RESOURCES_PATH = app.isPackaged
  ? path.join(process.resourcesPath, 'resources')
  : path.join(__dirname, '../../../resources');

const filePath = path.join(RESOURCES_PATH, 'Configuration.json');

export default async function ReadConfiguration(): Promise<Configuration> {
  try {
    const data = await fsPromises.readFile(filePath, 'utf8');
    const dataObject: Configuration = JSON.parse(data);
    return dataObject;
  } catch (err) {
    console.error(err);
    // Handle the error as needed
    throw err; // or return a sensible default if applicable
  }
}
