import { BrowserWindow } from 'electron';
import * as ClientEventHandlers from './clientEventHandlers';
import registerEvent from '../IPC/RegisterEvent';
import { ErrorMessage } from '../../shared/Types/ErrorMessage';
import createStackTraceFromException from '../../shared/utils/StackTrace.utils';
import { Report } from '../../shared/Types/Report.type';

// Uses for registering ipc events (client requests) with a functions.

export default function setIpcRoutes(
  // eslint-disable-next-line no-undef
  ipcMain: Electron.IpcMain,
  browserWindow: BrowserWindow
) {
  async function forwardErrorsToClient<T>(
    action: () => Promise<T>
  ): Promise<T | null> {
    try {
      return action();
    } catch (ex: any) {
      // eslint-disable-next-line camelcase
      const err_msg: ErrorMessage = {
        stringMessage: `Error from server: ${createStackTraceFromException(
          ex
        )}`,
      };
      try {
        ClientEventHandlers.handleErrorMessage(browserWindow, err_msg);
      } catch (exc: any) {
        console.log(
          // eslint-disable-next-line prettier/prettier
          `Exception while handling exception. ${createStackTraceFromException(exc)}`
        );
      }
      return null;
    }
  }

  registerEvent('check_ver', ipcMain, async () => {
    return forwardErrorsToClient(async () =>
      ClientEventHandlers.handleCheckVer()
    );
  });

  registerEvent('start_tester', ipcMain, async (param) => {
    await forwardErrorsToClient(async () =>
      ClientEventHandlers.handleRequestStartingTester(param)
    );
    return null;
  });

  registerEvent('create_report', ipcMain, async (param: Report) => {
    ClientEventHandlers.handleRequestPdfReport(param);
    return null;
  });

  registerEvent('open_reports_folder', ipcMain, async () => {
    ClientEventHandlers.handleOpenReportsFolder();
    return null;
  });

  registerEvent('get_platform', ipcMain, async () => {
    return ClientEventHandlers.handlePlatformRequest();
  });
}
