/* eslint-disable import/no-cycle */
/* eslint-disable no-use-before-define */
import { SerialPort } from 'serialport';
import { SerialPortHandle } from '../../shared/Types/Serial';
import TesterManager from '../SystemTests/TetserManage';
import ReadConfiguration from '../utils/Configurator';

export default class SerialPortManager {
  private static instance: SerialPortManager;

  private ecarosPort: SerialPortHandle = { options: { path: '', baudRate: 0 } };

  private cmodPort: SerialPortHandle = { options: { path: '', baudRate: 0 } };

  constructor() {
    // eslint-disable-next-line promise/catch-or-return
    this.setConfiguration().then(this.ConnectPorts);
  }

  public static getInstance(): SerialPortManager {
    if (!SerialPortManager.instance) {
      SerialPortManager.instance = new SerialPortManager();
    }
    return SerialPortManager.instance;
  }

  private async setConfiguration() {
    const config = await ReadConfiguration();
    this.ecarosPort = { options: config.SerialPorts.Ecaros };
    this.cmodPort = { options: config.SerialPorts.CMod };
  }

  public ConnectPorts() {
    this.ecarosPort.port = new SerialPort(this.ecarosPort.options);
    this.ecarosPort.port.on('data', TesterManager.OnLogReceived);

    this.cmodPort.port = new SerialPort(this.cmodPort.options);
    this.cmodPort.port.on('data', TesterManager.OnDiscreteStatusReceived);
  }

  public DisconnectPorts() {
    this.ecarosPort.port?.close();
    this.cmodPort.port?.close();
  }

  public Write(data: string) {
    this.cmodPort.port?.write(data);
  }
}
