/* eslint-disable import/prefer-default-export */
import { app, shell } from 'electron';
import path from 'path';
import { execFile as child } from 'child_process';
import os from 'os';
import {
  Device,
  Report,
  Technician,
  TestReport,
} from '../../shared/Types/Report.type';
import { TestStatus } from '../../shared/Types/Tests.type';

const buildDeviceObject = (device: Device | undefined) => {
  return {
    Device: {
      DeviceName: 'ECarus',
      IafNumber: Number(device?.IAFNumber),
      PakaID: Number(device?.Paka),
      DeviceSerialNumber: Number(device?.serialNumber),
      Catalog: '0',
    },
  };
};

const buildTechnicianObject = (technician: Technician | undefined) => {
  return {
    Technician: {
      ID: Number(technician?.personalNumber),
      Rank: '',
      Name: technician?.name,
    },
  };
};

const setTestResult = (result: TestStatus) => {
  return result === TestStatus.Success ? 'SUCCESS' : 'FAILED';
};

const buildTestsObject = (tests: TestReport[]) => {
  return {
    Tests: tests.map((testReport, index) => {
      return {
        TestNumber: index + 1,
        IsManualValue: true,
        MinValue: 0,
        MaxValue: 0,
        Value: 0,
        Name: testReport.testName,
        Status: setTestResult(testReport.testResult),
      };
    }),
  };
};

const createJsonReport = (report: Report): string => {
  const { device, technician, testReport } = report;

  const fullReportObject = {
    ...buildDeviceObject(device),
    ...buildTechnicianObject(technician),
    ...buildTestsObject(testReport),
  };

  return JSON.stringify(fullReportObject);
};

const RESOURCES_PATH = app.isPackaged
  ? path.join(process.resourcesPath, 'resources')
  : path.join(__dirname, '../../../resources');

const getPdfExePath = () => {
  const platform = os.platform();
  const arch = os.arch();

  if (platform === 'win32' && arch === 'x64') {
    return `${RESOURCES_PATH}\\win-x64\\PdfGenerator`;
  }
  if (platform === 'linux') {
    if (arch.startsWith('arm64')) {
      return `${RESOURCES_PATH}\\linux-arm64\\PdfGenerator`;
    }
  }
  throw new Error("PdfGenerator exe wasn't found");
};

export const CreatePdfReport = (report: Report): Boolean => {
  const exeRunner = child(getPdfExePath(), [createJsonReport(report)]);
  return exeRunner.exitCode === 0;
};

export const OpenReportsFolder = () => {
  shell.openPath(path.join(app.getAppPath(), 'Reports'));
};
