import { BrowserWindow, MessageBoxOptions, dialog } from 'electron';
// eslint-disable-next-line import/no-cycle
import TesterManager from '../SystemTests/TetserManage';
import { TestRunner } from '../../shared/Types/Tests.type';
import { DataCode, OpCode, WriteToCMod } from '../CMod/CModManager';
import { SimStatus } from '../../shared/Types/LogTypes';

export function delay(ms: number) {
  // eslint-disable-next-line no-promise-executor-return
  return new Promise((resolve) => setTimeout(resolve, ms));
}

export function CheckSimHiLo(simHi: SimStatus, simLo: SimStatus) {
  const receivedLogSimHi = TesterManager.getInstance().EcarosLog?.simHi;
  const receivedLogSimLo = TesterManager.getInstance().EcarosLog?.simLo;
  const receivedDiscreteSimHi =
    TesterManager.getInstance().EcarosDiscretes?.simHi;
  const receivedDiscreteSimLo =
    TesterManager.getInstance().EcarosDiscretes?.simLo;

  const result =
    receivedLogSimHi === simHi &&
    receivedLogSimLo === simLo &&
    receivedDiscreteSimHi === simHi &&
    receivedDiscreteSimLo === simLo;
  if (!result) {
    throw new Error(
      `Expected SimHi: ${simHi}, received: ${receivedLogSimHi}` +
        `Expected SimLo: ${simLo}, received: ${receivedLogSimLo}`
    );
  }

  return {
    result: true,
    message: '',
  };
}

export async function HeightSimulation(
  height: number,
  heightCode: DataCode,
  govaComm: OpCode,
  simHi: SimStatus,
  simLo: SimStatus
) {
  WriteToCMod(govaComm, heightCode);
  await delay(10);
  try {
    if (TesterManager.getInstance().EcarosLog?.digitalHeight !== height)
      throw new Error('   ');
    const simResult = CheckSimHiLo(simHi, simLo);
    return Promise.resolve(simResult);
  } catch (error) {
    return Promise.resolve({
      result: false,
      message: (error as Error).message,
    });
  }
}

export function CheckCorridors(hi: number, lo: number) {
  const receivedHi = TesterManager.getInstance().EcarosLog?.hi;
  const receivedLo = TesterManager.getInstance().EcarosLog?.lo;

  const result = receivedHi === hi && receivedLo === lo;
  if (!result) {
    throw new Error(
      `Expected Hi: ${hi}, received: ${receivedHi}` +
        `Expected Lo: ${lo}, received: ${receivedLo}`
    );
  }

  return {
    result: true,
    message: '',
  };
}

export async function RequestUserInput(
  mainWindow: BrowserWindow,
  testName: string,
  question: string,
  errorMsg: string
) {
  const buttons = ['', ''];

  const dialogOptions: MessageBoxOptions = {
    type: 'question',
    buttons,
    message: question,
    title: 'Ecaros Tester',
  };

  const userResponse = dialog.showMessageBoxSync(mainWindow, dialogOptions);

  if (userResponse === 1) throw new Error(errorMsg);
  return Promise.resolve({
    testName,
    result: true,
    message: '',
  });
}

export async function RequestUserAttention(
  mainWindow: BrowserWindow,
  msg: string
) {
  const dialogOptions: MessageBoxOptions = {
    type: 'question',
    buttons: [''],
    message: msg,
    title: 'Ecaros Tester',
  };

  dialog.showMessageBoxSync(mainWindow, dialogOptions);
}

export async function DisplayATPResult(
  mainWindow: BrowserWindow,
  result: Boolean
) {
  const failMsg = '  ,  ';
  const passMsg = '   ,  ';

  if (result) RequestUserAttention(mainWindow, passMsg);
  // eslint-disable-next-line no-use-before-define
  else DisplayErrorMsg(mainWindow, failMsg);
}

export async function DisplayErrorMsg(
  mainWindow: BrowserWindow,
  error: string
) {
  const dialogOptions: MessageBoxOptions = {
    type: 'error',
    buttons: [''],
    message: error,
    title: 'Ecaros Tester',
  };

  dialog.showMessageBoxSync(mainWindow, dialogOptions);
}

export function OferHeightSimulationTest(
  height: number,
  heightCode: DataCode,
  govaComm: OpCode,
  simHi: number,
  simLo: number
): TestRunner {
  return {
    name: `  ${height}`,
    testFunc: async () => {
      const result = await HeightSimulation(
        height,
        heightCode,
        govaComm,
        simHi,
        simLo
      );
      return {
        testName: `  ${height}`,
        ...result,
      };
    },
  };
}

export function YassurHeightSimulationTest(
  height: number,
  heightCode: DataCode,
  govaComm: OpCode,
  simHi: SimStatus,
  simLo: SimStatus,
  sound: string,
  mainWindow: BrowserWindow
) {
  return {
    name: `  ${height}`,
    testFunc: async () => {
      const testName = `  ${height}`;
      const result = await HeightSimulation(
        height,
        heightCode,
        govaComm,
        simHi,
        simLo
      );

      try {
        RequestUserInput(
          mainWindow,
          testName,
          sound === 'Silence'
            ? '     ?'
            : `  ${sound}`,
          '  '
        );
        return {
          testName,
          ...result,
        };
      } catch (error) {
        return {
          testName,
          result: false,
          message: (error as Error).message,
        };
      }
    },
  };
}

export function TestSiniUpDn(
  siniName: string,
  opCode: OpCode,
  hi: number,
  lo: number,
  simHi: number,
  simLo: number
) {
  return {
    name: `  ${siniName}`,
    testFunc: async () => {
      WriteToCMod(opCode, DataCode.DiscreteOn);
      WriteToCMod(opCode, DataCode.DiscreteOff);

      const testName = `  ${siniName}`;

      try {
        CheckCorridors(hi, lo);
        CheckSimHiLo(simHi, simLo);
        return {
          testName,
          result: true,
          message: '',
        };
      } catch (error) {
        return {
          testName,
          result: false,
          message: (error as Error).message,
        };
      }
    },
  };
}

