export function TestStepSelectUpDwn(
  direction: string,
  hi: number,
  lo: number,
  mainWindow: BrowserWindow,
  sound?: string,
  simHi: number = -1,
  simLo: number = -1
) {
  return {
    name: ` Step Select ${direction}`,
    testFunc: async () => {
      const attentionMsg = `   -Step Select ${direction}`;

      await RequestUserAttention(mainWindow, attentionMsg);

      try {
        CheckCorridors(hi, lo);
        if (simHi === -1 && simLo === -1) {
          return {
            testName: ` Step Select ${direction}`,
            result: true,
            message: '',
          };
        }

        CheckSimHiLo(simHi, simLo);

        if (sound) {
          RequestUserInput(
            mainWindow,
            '',
            `  ${sound}`,
            '   '
          );
        }

        return {
          testName: ` Step Select ${direction}`,
          result: true,
          message: '',
        };
      } catch (error) {
        return {
          testName: ` Step Select ${direction}`,
          result: false,
          message: (error as Error).message,
        };
      }
    },
  };
}

