export function FireDiscreteTest(
  aim: string,
  dataCode: DataCode,
  mainWindow: BrowserWindow
): TestRunner {
  const testName = ` ${aim}`;
  const question = `  ${aim}?`;
  const errMsg = ' fire  ';

  WriteToCMod(OpCode.Reliability_Fire_Pin, dataCode);
  return {
    name: testName,
    testFunc: async () => {
      try {
        const userResponse = await RequestUserInput(
          mainWindow,
          testName,
          question,
          errMsg
        );
        return userResponse;
      } catch (error) {
        return {
          testName,
          result: false,
          message: (error as Error).message,
        };
      }
    },
  };
}

export function ReliabilityDiscreteTest(
  mode: string,
  sound: string,
  dataCode: DataCode,
  mainWindow: BrowserWindow
): TestRunner {
  const testName = ` ${mode} Reliability `;
  const question = `  ${sound}?`;
  const errMsg = ' Reliability  ';

  WriteToCMod(OpCode.Reliability_Fire_Pin, dataCode);
  return {
    name: testName,
    testFunc: async () => {
      try {
        CheckSimHiLo(SimStatus.Off, SimStatus.Off);
        const userResponse = await RequestUserInput(
          mainWindow,
          testName,
          question,
          errMsg
        );
        return userResponse;
      } catch (error) {
        return {
          testName,
          result: false,
          message: (error as Error).message,
        };
      }
    },
  };
}

export function YassurMagaDiscreteTest(
  discrete: OpCode,
  dataCode: DataCode,
  modeString: string,
  discreteName: string,
  simHi?: SimStatus,
  simLo?: SimStatus
) {
  return {
    name: `   ${discreteName}`,
    testFunc: async () => {
      const testName = `   ${discreteName}`;
      WriteToCMod(discrete, dataCode);
      if (
        (simHi &&
          TesterManager.getInstance().EcarosDiscretes?.simHi === simHi) ||
        (simLo && TesterManager.getInstance().EcarosDiscretes?.simLo === simLo)
      )
        return {
          testName,
          result: true,
          message: '',
        };
      return {
        testName,
        result: false,
        message: `   ${modeString}`,
      };
    },
  };
}
