import { BrowserWindow } from 'electron';
import emitToClient from '../IPC/EmitToClient';
import { ErrorMessage } from '../../shared/Types/ErrorMessage';
import TesterManager from '../SystemTests/TetserManage';
import { Report } from '../../shared/Types/Report.type';
import { CreatePdfReport, OpenReportsFolder } from '../utils/PDFGenerator';

export function handleCheckVer(): string[] {
  TesterManager.getInstance().CheckVersion();
  return TesterManager.getInstance().Tests.map((test) => test.name);
}

export function handleRequestStartingTester(startRequest: string[]): void {
  TesterManager.getInstance().StartTester(startRequest);
}

export function handleErrorMessage(
  browserWindow: BrowserWindow,
  data: ErrorMessage
): void {
  emitToClient(browserWindow, 'error_message', data);
}

export function handleRequestPdfReport(report: Report): void {
  CreatePdfReport(report);
}

export function handleOpenReportsFolder(): void {
  OpenReportsFolder();
}

export function handlePlatformRequest(): string {
  return TesterManager.getInstance().getHelicopterType();
}
