export enum TestStatus {
  'Success',
  'Failed',
  'Progress',
  'Waiting',
}

export const StatusColors = {
  [TestStatus.Success]: '#4CAF50', // Green
  [TestStatus.Failed]: '#F44336', // Red
  [TestStatus.Progress]: '#FFC107', // Amber
  [TestStatus.Waiting]: '#2196F3', // Blue
};

export type Test = [string, TestStatus, boolean];
export type Tests = Test[];

export type TestResult = {
  testName: string;
  result: boolean;
  message: string;
};

export type TestRunner = {
  name: string;
  testFunc: () => Promise<TestResult>;
};
