/* eslint-disable no-bitwise */
/* eslint-disable no-use-before-define */
import {
  HelicopterPlatform,
  ParsedDiscretes,
  ParsedLog,
} from '../../shared/Types/LogTypes';

function splitData(data: string | null): string[] | null {
  return data === null ? null : data.split('<\n><\r>');
}

function parseIntegerFromSubstring(
  str: string,
  startIndex: number,
  endIndex?: number
): number {
  return parseInt(str.substring(startIndex, endIndex), 10);
}

export function parseDiscretesLog(data: string | null): ParsedDiscretes | null {
  const dataArgs = splitData(data);
  if (!dataArgs) return null;
  return parseSimHiLo(dataArgs[0], dataArgs[1]);
}

export function recognizeHelicopterPlatform(
  data: string | undefined
): HelicopterPlatform {
  if (typeof data === 'string' && data.includes('1.42')) {
    return 'Yassur';
  }
  return 'Ofer'; // Ofer version is 2.01. For now, No need to support more platforms than there is now.
  // If you want to add more platforms, just add an IF statement.
}

export function parseLog(
  data: string | null,
  isYassur: boolean
): ParsedLog | null {
  const dataArgs = splitData(data);
  if (!dataArgs) return null;
  return {
    ...(isYassur
      ? parseHeightForYassur(dataArgs[0], dataArgs[6])
      : parseHeightForOfer(dataArgs[0])),
    ...parseHiLo(dataArgs[1], dataArgs[2]),
    ...parseSimHiLo(dataArgs[3], dataArgs[4]),
    ...parseDiscretes(dataArgs[5]),
    ...parseVersion(dataArgs[5]),
  };
}

function parseHeightForYassur(
  digitalHeightStr: string,
  analogHeightStr: string
) {
  return {
    digitalHeight: parseIntegerFromSubstring(digitalHeightStr, 1),
    analogHeight: parseIntegerFromSubstring(analogHeightStr, 0, 4),
  };
}

function parseHeightForOfer(heightStr: string) {
  return {
    digitalHeight: parseIntegerFromSubstring(heightStr, 7),
    analogHeight: 0,
  };
}

function parseHiLo(hiStr: string, loStr: string) {
  return {
    hi: parseIntegerFromSubstring(hiStr, 3),
    lo: parseIntegerFromSubstring(loStr, 3),
  };
}

function parseSimHiLo(simHiStr: string, simLoStr: string) {
  return {
    simHi: parseIntegerFromSubstring(simHiStr, 7),
    simLo: parseIntegerFromSubstring(simLoStr, 7),
  };
}

function parseDiscretes(discretesStr: string) {
  const byte0 = discretesStr.charCodeAt(0);
  const byte1 = discretesStr.charCodeAt(1);
  return {
    iReliability: (byte0 >> 6) & 1,
    iSiniUp: (byte0 >> 2) & 1,
    iSiniDw: (byte0 >> 1) & 1,
    iSIM_HI: (byte1 >> 3) & 1,
    iSIM_LO: (byte1 >> 4) & 1,
  };
}

function parseVersion(versionStr: string) {
  return {
    version: versionStr.substring(3),
  };
}
